.inline-block {
  margin-block: 0;
  display: inline-block;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  width: max-content;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(65, 146, 54, 0.288);
  color: black;
  font-size: 0.7rem;
  text-align: center;
  border-radius: 6px;
  padding: 5px 10px;
  transition: all 500ms ease-in-out;
  top: -10px;
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.form-danger {
  height: fit-content;
  color: red;
  font-size: 0.8rem;
}

.mdl-msg {
  z-index: 100;
  width: 100%;
  text-align: center;
  position: fixed;
  top: 0em;
  transform: translateY(-50px);
  transition: all 300ms;

  /* transition-delay: 300ms; */

  opacity: 0;
  text-align: center;
  /* animation: normal;
      animation-duration: 200ms; */
  /* bottom: 0.1; */
  /* position: absolute;
      /* bottom: 2em; */
}

.mdl-error {
  background-color: rgba(230, 30, 20, 0.3);
}

.mdl-ok {
  background-color: rgba(74, 202, 35, 0.3);
}

.mdl-msg-active {
  z-index: 100;
  transform: translateY(0);
  opacity: 1;
  /* transition-delay: 150ms; */
  transition-duration: 500ms;
}
